import styled from "styled-components";

import { MenuBarButton } from "../MenuBarButton";
import { colors, sizes, spacingMap } from "../styleguide";
import { pxToRem } from "../utils";

export const SidebarWrapper = styled.nav`
	box-sizing: border-box;
	position: fixed;
	left: 0;
	top: 0;
	width: ${sizes.sidebarWidth};
	bottom: 0;
	overflow: auto;
	background:${colors.white}BF;
	border-radius: ${spacingMap.sm};
	margin: ${spacingMap.xs};
	box-shadow: 0 2px 8px -2px ${colors.black}1A,0 0 1px 0 ${colors.black}52;
	ul {
   		height: fit-content;
  	}
	& li { // To position properly the subnav item active right bar
		position: relative;
	}
`;

export const SidebarItemsWrapper = styled.ul`
	box-sizing: border-box;
	padding: ${spacingMap.xs};
	height: 100%;
	margin: 0;

	& a {
		display: block;
		text-decoration: none;

		&:focus-visible {
			opacity: 1;
			outline: 2px solid ${colors.midBlue};
		}

		&:hover {
			opacity: 1;
		};

		&:visited {
			color: initial;
			text-decoration: none;
		};

		+ ul { // subnav item
			opacity: 1;
			border: solid 1px transparent;
			border-top: 0;
			border-radius: 0 0 8px 8px;
		}

		&[aria-current="page"] + ul { // subnav item active
			opacity: 1;
			color: ${colors.black};
			background-color: ${colors.lightGray}80;
			border: solid 1px ${colors.white};
			border-top: none;
			border-radius: 0 0 8px 8px;
			display: block;
		}

		&[aria-current="page"] + ul::after { // subnav item active right bar
			content: '';
			position: absolute;
			width: 4px;
			height: 100%;
			border-radius: 2px 0 0 2px;
			right: -8px;
			opacity: 1;
			background-color: ${colors.black};
			color: ${colors.black};
			top: 0;
		}
	}

	& li {
		list-style: none;
	}
`;

export const SettingsItemsWrapper = styled(SidebarItemsWrapper)`
	bottom: ${pxToRem(5)};
	position: fixed;
	width: ${sizes.sidebarWidth};
	margin-bottom: ${spacingMap.xs};
`;

export const StyledWebsitePicker = styled(MenuBarButton)`
	width: -webkit-fill-available;
	border-radius: 8px;
	justify-content: space-between;
	padding: ${spacingMap.xs};
	background: transparent;
`;
